import { API, OnUserChangedFunc, UserChangedListener } from "./api/api";
import {AuthUser, Credentials, LoginResponse, Registration, RegistrationResponse, RetrievePasswordFunc, RetrievePasswordResponse, UserLogoutFunc, Verification, VerificationResponse} from './api/common';
import { DBapi } from "./api/DBapi";

import { TypeformDBapi } from "./api/TypeormDBapi";

import mitt, {Emitter} from 'mitt';

const db: DBapi = new TypeformDBapi();

const _blankUser = function() :AuthUser {
    return {
        name: "",
        email: "",
        company: ""
    };
}
class api2dbapi implements API {
    db: DBapi;
    current_user: AuthUser =_blankUser();
    emitter : Emitter = mitt();

    constructor(db: DBapi){
        this.db = db;
    }

    register(registration: Registration) :Promise<RegistrationResponse>{
        return this.db.register(registration);
    }

    verify(verification : Verification) :Promise<VerificationResponse>{
        return this.db.verify(verification)
    }

    login(credentials : Credentials) :Promise<LoginResponse>{
        return this.db.login(credentials).then((response: LoginResponse)=>{
            if(response.authUser){
                this.current_user = response.authUser;
                this.emitter.emit("userChange",this.current_user);
            }
            return response;
        })
    }
    logout() : Promise<boolean>{
        try{
            return this.db.logout(this.current_user)
        }finally{
            this.current_user = _blankUser();
            this.emitter.emit("userChange",this.current_user);
        }
    }
    retreivePassword(emailAddress: string) : Promise<RetrievePasswordResponse>{
        return this.db.retreivePassword(emailAddress);
    }
    
    onUserChanged(callback: UserChangedListener){
        this.emitter.on("userChange", callback)
    }    
}
const _api = new api2dbapi(new TypeformDBapi());

export const api: API = _api;

