import { Column, Entity, JoinColumn, ManyToOne, OneToMany } from "typeorm";
import { BaseEntity, SortableBaseEntity } from "./BaseEntity";


@Entity()
export class Company extends BaseEntity {
    @ManyToOne(()=>Facility, facility => facility.company)
    facilities: Facility[]
}

@Entity()
export class Facility extends SortableBaseEntity {

    @OneToMany(()=>Company, company => company.facilities)
    @JoinColumn()
    company: Company;
   
    @ManyToOne(()=>Survey, survey => survey.facility)
    surveys: Survey[]
}

@Entity()
export class Survey extends BaseEntity {
    @OneToMany(()=>Facility, facility=>facility.surveys)
    @JoinColumn()
    facility: Facility;
}