const sha256 = (message: string) => {
    const encoder = new TextEncoder();
    const data = encoder.encode(message);
    return window.crypto.subtle.digest('SHA-256', data);
}
  
const bufferToBase64UrlEncoded = (input: ArrayBuffer) => {
    const bytes = new Uint8Array(input);
    return urlEncodeBase64(window.btoa(String.fromCharCode.apply(null, bytes)));
}
  
const urlEncodeBase64 = (input: string) => {
    const chars = {'+': '-', '/': '_', '=': ''};
    return input.replace(/[\+\/=]/g, m => chars[m]);
}

export const hash = (input: string) :PromiseLike<string> =>{
    return sha256(input).then(a=>bufferToBase64UrlEncoded(a));
}