import { Column, CreateDateColumn, UpdateDateColumn, PrimaryGeneratedColumn, Unique, OneToMany, ManyToOne } from "typeorm";

export abstract class BaseEntity {
    @PrimaryGeneratedColumn()
    id: number;

    @CreateDateColumn()
    createdAt: Date;
    
    @UpdateDateColumn()
    updatedAt: Date;

    @Column()
    name: string;
}

export abstract class SortableBaseEntity extends BaseEntity{

    @Column({default: 100})
    sort_order: number;
}