import { LitElement, html, css, customElement, property, TemplateResult, query } from 'lit-element';
import '@material/mwc-button';
import '@material/mwc-dialog';
import '@material/mwc-textfield';
import '@material/mwc-formfield';
import '@material/mwc-checkbox';
import { Dialog } from '@material/mwc-dialog';
import { TextField } from '@material/mwc-textfield';
import { Checkbox } from '@material/mwc-checkbox';
import '@material/mwc-circular-progress-four-color';

import { api } from './api-client';
import { AuthUser } from './api/common';

export enum Form {
  Alert,
  Login,
  Logout,
  Register,
  RegisterConfirm,
  ForgottenPassword,
  ForgottenPasswordConfirm,
  Clear,
  Verification,
  VerificationSuccess
}


function emailIsValid(email) {
  return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
}

interface Alert {
  title: string,
  content: TemplateResult,
  next: Form
}

@customElement('epd-login-register')
export class EpdLoginRegisterElement extends LitElement {

  static styles = css`
    mwc-checkbox {
    --mdc-checkbox-unchecked-color: red;
  }
`;

  @query('#new_password_textfield') private newPassword!: TextField;
  @query('#retype_password_textfield') private retypePassword!: TextField;
  @query('#email_textfield') private loginEmail!: TextField;
  @query('#password_textfield') private loginPassword!: TextField;
  @query('#fullname_textfield') private fullName!: TextField;
  @query('#company_textfield') private company!: TextField;
  @query('#verification_code_textfield') private verificationCode!: TextField;
  @query('mwc-dialog') private dialog!: Dialog;
  @query('#terms_conditions_checkbox') private termsConditions!: Checkbox
  @query('#privacy_policy_checkbox') private privacyPolicy!: Checkbox

  @property({ type: Boolean })
  open = true;

  @property({ attribute: false })
  form = Form.Login;

  @property({ type: Boolean, attribute: false })
  submitting = false;

  private alert: Alert = {
    title: "Hello",
    content: html``,
    next: Form.Clear
  };

  private enteredEmail = ""; // not @property

  createRenderRoot() {
    return this;
  }
  render() {
    switch (this.form) {
      case Form.Alert:
        return this.alertForm;
      case Form.Login:
        return this.loginForm;
      case Form.Logout:
        return this.logoutForm;
      case Form.Register:
        return this.registerForm;
      case Form.RegisterConfirm:
        return this.registrationConfirmForm;
      case Form.ForgottenPassword:
        return this.forgottenPasswordForm;
      case Form.ForgottenPasswordConfirm:
        return this.forgottenPasswordConfirmForm;
      case Form.Verification:
        return this.verificationForm;
      case Form.VerificationSuccess:
        return this.verificationSuccessForm;
      case Form.Clear:
        return html``;

      default:
        return html`<h1>Dunno what form</h1>`

    }
  }

  private get email_textfield(): TemplateResult {
    return html`
    <mwc-textfield
    id="email_textfield"
    type="email"
        label="Email"
        name="email"
        value=${this.enteredEmail}
        required
        .validityTransform="${this.validateEmail}"
        dialogInitialFocus>
    </mwc-textfield>`;
  }

  private get loginForm(): TemplateResult {
    let actions = html`<mwc-button 
        @click=${this._onClickLogin}
          slot="primaryAction" 
          icon="login"
          raised
          label="Sign In">
          </mwc-button>
      <mwc-button 
        @click=${this._onClickNoAccount}
        slot="secondaryAction" 
          dialogAction="cancel"
          icon="create"
          label="Not Registered?">
        </mwc-button>
        <mwc-button 
        @click=${this._onClickForgetPassword}
          dialogAction="cancel"
          icon="help"
          label="Forget Password?">
      </mwc-button>
    `
    if (this.submitting) {
      actions = html`<mwc-circular-progress-four-color indeterminate></mwc-circular-progress-four-color>`;
    }

    return html`
    <mwc-dialog id="login_dialog" open="${this.open}" heading="LOGIN" stacked>
    <div>
      ${this.email_textfield}
  <div>
      <mwc-textfield
      id="password_textfield"
          type="password"
          label="Password"
          required>
      </mwc-textfield>
  </div>
  </div>
  ${actions}
    </mwc-dialog>
    `;
  }

  private get logoutForm(): TemplateResult {
    return html`
    <mwc-dialog id="logout_dialog" open heading="CONFIRM SIGN OUT" stacked>
      <p>Sign out now?</p>
      <mwc-button 
        slot="primaryAction"
        @click=${this._onClickSignOut}
        icon="check"
        raised
        label="Sign Out">
      </mwc-button>
      <mwc-button 
       slot="secondaryAction" 
            dialogAction="cancel"
            icon="cancel"
            label="Cancel">
          </mwc-button>
    </mwc-dialog> `;
  }

  private get registerForm(): TemplateResult {
    let actions = html`
          <mwc-button 
          @click=${this._onClickRegister}
            slot="primaryAction" 
            icon="create"
            raised
            label="Register">
          </mwc-button>
          <mwc-button 
          slot="secondaryAction" 
          @click=${this._onClickAlreadyRegistered}
            dialogAction="cancel"
            icon="login"
            dense
            label="Already Registered?">
          </mwc-button>
    `;
    if (this.submitting) {
      actions = html`<mwc-circular-progress-four-color indeterminate></mwc-circular-progress-four-color>`;
    }
    return html`
    <mwc-dialog id="register_dialog" ?open="${this.open}" heading="REGISTER" stacked>
      <div>
        <div>
          ${this.email_textfield}
        </div>
      <div>
          <mwc-textfield
          id="fullname_textfield"
          minlength="5"
          type="text"
              label="Full Name"
              required>
          </mwc-textfield>
      </div>
      <div>
          <mwc-textfield
          id="company_textfield"
          type="text"
          required
          minlength="3"
              label="Company">
          </mwc-textfield>
      </div>
      <div>
        Check to indicate you have<br>
        read, understand and accept the:
      </div>
      <div>
      <mwc-formfield>
        <mwc-checkbox id="terms_conditions_checkbox" ></mwc-checkbox>
        <span><a href="#" style="text-decoration: none"><mwc-button label="Terms and Conditions"></mwc-button></a></span>
      </mwc-formfield>
      </div>
      <div>
      <mwc-formfield>
        <mwc-checkbox id="privacy_policy_checkbox" ></mwc-checkbox>
        <span><a href="#" style="text-decoration: none"><mwc-button label="Privacy Policy"></mwc-button></a></span>
      </mwc-formfield>
      </div>
  </div>
        ${actions}
    </mwc-dialog>
    `;
  }

  private get verificationForm(): TemplateResult {
    let actions = html`
          <mwc-button 
        @click=${this._onClickVerify}
          slot="primaryAction" 
          icon="check"
          raised
          label="Confirm">
        </mwc-button>
        <mwc-button 
        slot="secondaryAction"
          dialogAction="cancel"
          icon="cancel"
          dense
          label="Cancel">
        </mwc-button>
    `;
    if (this.submitting) {
      actions = html`<mwc-circular-progress-four-color indeterminate></mwc-circular-progress-four-color>`;
    }
    return html`
    <mwc-dialog id="verification_dialog" ?open="${this.open}" heading="Email Verification" stacked>
            <div>
          <div>
            ${this.email_textfield}
          </div>
          <div>
              <mwc-textfield
              id="verification_code_textfield"
                  type="text"
                  label="Code"
                  .validityTransform = "${this.validateVerificationCode}"
                  minLength=6
                  maxLength=6
                  required
                  >
              </mwc-textfield>
        </div>
        <div>
            <mwc-textfield
            id="new_password_textfield"
                type="password"
                label="New Password"
                .validityTransform = "${this.validateNewPassword}"
                required
                >
            </mwc-textfield>
      </div>       <div>
            <mwc-textfield
            id="retype_password_textfield"
                type="password"
                label="Retype Password"
                .validityTransform = "${this.validateRetypeNewPassword}"
                required
                >
            </mwc-textfield>
      </div>
      </div>
      ${actions}

      </mwc-dialog>
    `;
  }
  private get forgottenPasswordForm(): TemplateResult {
    let actions = html`
        <mwc-button 
          @click=${this._onClickRetrieveForgottenPassword}
            slot="primaryAction" 
            icon="email"
            raised
            label="Email me">
        </mwc-button>
        <mwc-button 
          @click=${this._onClickCancel}
          slot="secondaryAction" 
            dialogAction="cancel"
            icon="cancel"
            label="Cancel">
          </mwc-button>
        </mwc-dialog>
   `;
    if (this.submitting) {
      actions = html`<mwc-circular-progress-four-color indeterminate></mwc-circular-progress-four-color>`;
    }

    return html`
    <mwc-dialog id="forgotten_password_dialog" ?open=${this.open} heading="FORGOTTEN PASSWORD" stacked>
    <div>
      <div>
      ${this.email_textfield}
  </div>
  </div>
  ${actions}
    </mwc-dialog>
    `;
  }
  private get verificationSuccessForm(): TemplateResult {
    return html`
    <mwc-dialog id="verification_success_dialog" open heading="ACCOUNT VERIFIED" stacked>
      <p>You can sign in using your new password.</p>
      <mwc-button 
        slot="primaryAction"
        @click=${this._onClickLoginNext}
        icon="check"
        dialogAction="close"
        raised
        label="OK">
      </mwc-button>
    </mwc-dialog>
    `;
  }
  private get forgottenPasswordConfirmForm(): TemplateResult {
    return html`
    <mwc-dialog id="forgotten_password_confirm_dialog" open heading="PASSWORD RESET EMAIL SENT" stacked>
      <p>If the email is registered, a verification code has been sent.</p>
      <p>Check your spam folder if message not in your inbox soon.</p>
      <mwc-button 
        slot="primaryAction"
        @click=${this._onClickVerifyNext}
        icon="check"
        raised
        label="OK">
      </mwc-button>
    </mwc-dialog>
    `;
  }

  private get registrationConfirmForm(): TemplateResult {
    return html`
    <mwc-dialog id="registration_confirm_dialog" open heading="REGISTRATION EMAIL SENT" stacked>
      <p>A registration email message has been sent.</p>
      <p>Check your spam folder if message not in your inbox soon.</p>
      <mwc-button 
        slot="primaryAction"
        @click=${this._onClickVerifyNext}
        icon="check"
        raised
        label="OK">
      </mwc-button>
    </mwc-dialog>
    `;
  }

  private get alertForm(): TemplateResult {
    return html`
    <mwc-dialog id="registration_confirm_dialog" open heading="${this.alert.title}" stacked>
      ${this.alert.content}
      <mwc-button 
        slot="primaryAction"
        @click=${this._onClickAlertOK}
        icon="check"
        raised
        label="OK">
      </mwc-button>
    </mwc-dialog>
    `;
  }
  private validateEmail = (value: string, vState: ValidityState): Partial<ValidityState> => {
    let isValid = emailIsValid(value);
    this.loginEmail.setCustomValidity(isValid ? "" : 'Email not accepted!');
    if (!vState.valid) {
      return {};
    }
    return {
      valid: isValid,
      customError: !isValid,
    };
  }

  private validateNewPassword = (value: string, vState: ValidityState): Partial<ValidityState> => {
    let isValid = true;
    let message = "";
    if (value.length < 6) {
      isValid = false;
      message = "Must be at least 6 characters";
    } else if (value.match(/(.)\1{2,}/)) {
      isValid = false;
      message = "Too many repeating characters";
    } else if ((value.length <= 8 && value.match(/^[\w0-9]*$/))
      || (value.length <= 10 && (value.match(/^[\w]*$/) || value.match(/^[0-9]*$/)))
    ) {
      isValid = false;
      message = "Password is too simple";
    }
    this.newPassword.setCustomValidity(message);
    return {
      valid: isValid,
      customError: !isValid
    };
  }

  private validateVerificationCode = (value: string, vState: ValidityState): Partial<ValidityState> => {
    let isValid = value.match(/^[0-9]{6}$/) ? true : false;
    this.verificationCode.setCustomValidity(isValid ? "" : "Enter code from your email");
    return {
      valid: isValid,
      customError: !isValid,
    };
  }
  private validateRetypeNewPassword = (value: string, vState: ValidityState): Partial<ValidityState> => {
    let isValid = value === this.newPassword.value;
    this.retypePassword.setCustomValidity(isValid ? "" : "Passwords don't match");
    return {
      valid: isValid,
      customError: !isValid,
    };
  }

  private _onClickAlertOK() {
    this.form = this.alert.next;
  }

  private _onClickVerifyNext() {
    this.form = Form.Verification;
  }

  private _onClickVerify() {
    if (!this.loginEmail.checkValidity()) {
      return false;
    }
    if (!this.verificationCode.checkValidity()) {
      return false;
    }
    if (!this.newPassword.checkValidity()) {
      return false;
    }
    if (!this.retypePassword.checkValidity()) {
      return false;
    }

    let verification = {
      email: this.loginEmail.value,
      code: this.verificationCode.value,
      password: this.newPassword.value
    }
    this.enteredEmail = verification.email;
    this.submitting = true;
    api.verify(verification).then((response) => {
      if (this.form === Form.Verification) {
        if (response.status === "ok") {
          this.form = Form.VerificationSuccess;
          return;
        }
        this.alert.content = html`<p>Invalid email or code</p>`;
        this.alert.next = Form.Verification;
        this.alert.title = "Verification problem";
        if (response.status === "error") {
          this.alert.content = html`<p>Something went wrong</p>`
        }
        this.form = Form.Alert;
      }
    }).finally(() => this.submitting = false)
    return true;
  }

  private _onClickLogin() {
    if (!this.loginEmail.checkValidity()) {
      return false;
    }
    if (!this.loginPassword.checkValidity()) {
      return false;
    }
    let credentials = {
      email: this.loginEmail.value,
      password: this.loginPassword.value
    }
    this.enteredEmail = credentials.email;
    this.submitting = true;
    api.login(credentials).then(response => {
      if (response.status === "ok") {
        this.form = Form.Clear;
        return;
      }

      let message = "Wrong email or password.";
      if (response.status === "error") {
        message = "Sorry. Something went wrong. (It's not your fault)"
      }
      this.alert.content = html`<p>${message}</p>`;
      this.alert.next = Form.Login;
      this.alert.title = "Sign in problem";
      this.form = Form.Alert;

    }).finally(() => this.submitting = false)

    return true;
  }

  private _onClickRegister() {
    if (!this.loginEmail.checkValidity()) {
      return false;
    }
    if (!this.fullName.checkValidity()) {
      return false;
    }
    if (!this.company.checkValidity()) {
      return false;
    }
    if (!this.termsConditions.checked) {
      return false;
    }
    if (!this.privacyPolicy.checked) {
      return false;
    }

    let registration = {
      email: this.loginEmail.value,
      fullName: this.fullName.value,
      company: this.company.value,
      termsConditionsAcceptedTime: new Date().getTime(),
      privacyPolicyAcceptedTime: new Date().getTime()
    };

    this.submitting = true;
    api.register(registration).then((response) => {
      if (this.form === Form.Register) {
        if (response.status === "ok") {
          this.enteredEmail = registration.email;
          this.form = Form.RegisterConfirm;
          return;
        }
        this.form = Form.Clear;
      }
    }).finally(() => this.submitting = false)
    return true;
  }

  private _onClickForgetPassword() {
    this.form = Form.ForgottenPassword;
    return true;
  }
  private _onClickRetrieveForgottenPassword() {
    let email = this.loginEmail.value;
    if (email && emailIsValid(email)) {
      this.enteredEmail = email;
      this.submitting = true;
      api.retreivePassword(email).then(response => {
        if (this.form === Form.ForgottenPassword) {
          this.submitting = false;
          this.form = Form.ForgottenPasswordConfirm;
        }
      })
      return true;
    }
    return false;
  }
  private _onClickCancel() {
    this.form = Form.Clear
    return true;
  }
  private _onClickSignOut() {
    api.logout();
    this.form = Form.Clear;
    return true;
  }
  private _onClickNoAccount() {
    this.form = Form.Register;
  }
  private _onClickAlreadyRegistered() {
    this.form = Form.Login;
  }
  private _onClickLoginNext() {
    this.form = Form.Login;
  }
  show(): void {
    this.submitting = false;
    let dialog = this.dialog;
    dialog && dialog.show && dialog.show();
  }

}


declare global {
  interface HTMLElementTagNameMap {
    'epd-login-register': EpdLoginRegisterElement;
  }
}
