import { LitElement, html, customElement, property, css, query } from 'lit-element';
import '@material/mwc-button';
import '@material/mwc-icon';
import { IconButton } from '@material/mwc-icon-button';
import '@material/mwc-icon-button';
import '@material/mwc-top-app-bar-fixed';
import '@material/mwc-menu';
import { Menu } from '@material/mwc-menu';
import '@material/mwc-list/mwc-list-item';

import './epd-concrete';
import './epd-mockup01';
import './epd-login-register';
import { EpdLoginRegisterElement, Form } from './epd-login-register';
import { api } from './api-client';
import { AuthUser } from './api/common';


const appname = "EPD Toolkit"

@customElement('epd-tool')
export class EpdToolElement extends LitElement {
  constructor() {
    super();
    api.onUserChanged((user: AuthUser) => {
      this.username = user.name;
      if (this.username === '') {
        this.content = html``;
      }
    });
    window.addEventListener('hashchange', () => this._navigatehash(), false);

  }
  @property({ attribute: false }) content = html``;

  @property({ attribute: false }) username = "";

  @query('#more-menu-button') private moreMenuButton!: IconButton;
  @query('#more-menu') private moreMenu!: Menu;
  @query('#epd-login-register') private loginRegister!: EpdLoginRegisterElement;


  static styles = css`
      .epd-tool {
        --mdc-theme-primary: #258add;
        --mdc-theme-secondary: #457494;
        max-width: 800px;
        width: 50%;

        --mdc-circular-progress-bar-color-1: #2196f3;
        --mdc-circular-progress-bar-color-2: #e91e63;
        --mdc-circular-progress-bar-color-3: #ffc107;
        --mdc-circular-progress-bar-color-4: #03dac5;
      }
      .required{
          --mdc-checkbox-unchecked-color: red;
      }
`;


  render() {
    let body = this._getMainPageContent();
    let buttons = html``;
    if (this.username) {
      this.content = html``
      body = html`<epd-mockup01></epd-mockup01>`
      if(false)
      buttons = html`<mwc-button
          @click=${this._onClickConcrete}
          id="concreteButton"
          outlined
          label="Concrete">
          </mwc-button>`
    }
    let verificationMenuItem = html``;
    if (!this.username) {
      verificationMenuItem = html`
            <mwc-list-item group="a" graphic="icon" @click=${this._onClickVerify}>
              <mwc-icon slot="graphic">check</mwc-icon>
              <span>Verify Account</span>
            </mwc-list-item>
      `;
    }
    let contextMenuItems = html``;
    if(this.username){
      contextMenuItems = html`
                  <mwc-list-item group="1" graphic="icon" > <mwc-icon slot="graphic">calculate</mwc-icon><span>Company</span></mwc-list-item>
            <mwc-list-item group="b" graphic="icon" > <mwc-icon slot="graphic">calculate</mwc-icon><span>Facility</span></mwc-list-item>
            <mwc-list-item group="b" graphic="icon" > <mwc-icon slot="graphic">calculate</mwc-icon><span>Survey</span></mwc-list-item>
`;
    }
    return html`<link rel="stylesheet" href="./mdc.typography.min.css">
    <div class="epd-tool">
      <mwc-top-app-bar-fixed id="bar">
        <mwc-icon-button icon="menu" slot="navigationIcon"></mwc-icon-button>
        <div slot="title" id="title">${appname}</div>
        <mwc-icon-button title="${this.username ? 'Sign Out' : 'Sign In'}" label="${this.username ? 'Sign Out' : 'Sign In'}" icon="${this.username ? 'logout' : 'login'}" slot="actionItems" class="notCenter" @click=${this._onClickSigninSignout}></mwc-icon-button>
          <mwc-icon-button id="more-menu-button" label="More..." icon="more_vert" slot="actionItems" class="notCenter" @click="${this._onClickMoreMenu}"></mwc-icon-button>
          <mwc-menu id="more-menu"
              .anchor=${this.moreMenuButton}
              corner="BOTTOM_LEFT">
            <mwc-list-item group="a" graphic="icon" @click=${this._onClickSigninSignout}>
              <mwc-icon slot="graphic">${this.username ? 'logout' : 'login'}</mwc-icon>
              <span>${this.username ? 'logout' : 'login'}</span>
            </mwc-list-item>
            ${verificationMenuItem}
            ${contextMenuItems}
          </mwc-menu>
        <div id="content">
          <div id="buttons">
            ${buttons}
          </div>
        </div>
      </mwc-top-app-bar-fixed>
      ${this.content}
      <epd-login-register id="epd-login-register" ?open=${this.username ? false : true}></epd-login-register>
      <br>
      ${body}
      </div>
    `;
  }


  private _getMainPageContent() {
    if (this.username) {
      return html``;
    }
    return html`
    <h3 class="mdc-typography--headline3">Welcome to ${appname}</h3>
    <p>Please <a href="#register">Register</a> or <a href="#login">Sign In</a> to continue.</p>
    `;
  }

  private _onClickConcrete() {
    this.content = html`<epd-concrete></epd-concrete>`
  }

  private _onClickSigninSignout() {
    if (this.username) {
      this.loginRegister.form = Form.Logout;
    } else {
      this.loginRegister.form = Form.Login;
    }
    this.loginRegister.show();
  }

  private _onClickVerify() {
    this.loginRegister.form = Form.Verification;
    this.loginRegister.show();
  };

  private _onClickRegister() {
    this.loginRegister.form = Form.Register;
    this.loginRegister.show();
  };


  private _onClickMoreMenu() {
    let moreMenu = this.moreMenu;
    moreMenu.anchor = this.moreMenuButton;
    moreMenu.show();
  }

  private _setNewHash(hash: URLSearchParams) {
    let s = hash.toString();
    if (s.length) {
      location.hash = "#" + s;
    } else {
      history.replaceState(null, null, ' ');
    }
  }
  private _navigatehash() {
    let hash = new URLSearchParams(location.hash.substring(1));
    if (hash.get("login") !== null) {
      hash.delete("login");
      this._setNewHash(hash);
      if (!this.username) {
        this._onClickSigninSignout();
        return;
      }
    }
    if (hash.get("register") !== null) {
      hash.delete("register");
      this._setNewHash(hash);
      if (!this.username) {
        this._onClickRegister();
        return;
      }
    }
  }

}

declare global {
  interface HTMLElementTagNameMap {
    'epd-tool': EpdToolElement;
  }
}
